import { Modal } from 'antd';
import { Icon } from 'assets/svg';
import { useState } from 'react';
import { Trans } from 'react-i18next';
import { BuyContent1, BuyContent2 } from './_Direction';
import ModalBody1 from './_ModalBody1';
import ModalBody2 from './_ModalBody2';
import Tab3, { PropsTab3ContentSection } from './_Tabs3';

const Content1 = () => {
  const [activeTab, setActiveTab] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOpenModel = (id: number) => {
    setActiveTab(id);
    setIsModalOpen(true);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="directtion-mobile__container">
      <ul className="directtion-mobile__step">
        {BuyContent1.map((b, i) => (
          <li key={i} onClick={() => handleOpenModel(i)}>
            <div>
              <p>
                <Trans i18nKey="Bước"></Trans> {i + 1}
              </p>
              <p>{b.title}</p>
            </div>
            <Icon.RightArrow />
          </li>
        ))}
      </ul>

      <Modal
        width={'100%'}
        open={isModalOpen}
        title={BuyContent1[activeTab].title}
        footer={<div className="ant-modal-footer__wrap"></div>}
        onCancel={handleCancel}
      >
        <ModalBody1 id={activeTab} onTabChange={setActiveTab} />
      </Modal>
    </div>
  );
};

const Content2 = () => {
  const [activeTab, setActiveTab] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOpenModel = (id: number) => {
    setActiveTab(id);
    setIsModalOpen(true);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="directtion-mobile__container">
      <ul className="directtion-mobile__step">
        {BuyContent2.map((b, i) => (
          <li key={i} onClick={() => handleOpenModel(i)}>
            <div>
              <p>
                <Trans i18nKey="Bước"></Trans> {i + 1}
              </p>
              <p>{b.title}</p>
            </div>
            <Icon.RightArrow />
          </li>
        ))}
      </ul>

      <Modal
        width={'100%'}
        open={isModalOpen}
        title={BuyContent2[activeTab].title}
        footer={<div className="ant-modal-footer__wrap"></div>}
        onCancel={handleCancel}
      >
        <ModalBody2 id={activeTab} />
      </Modal>
    </div>
  );
};

const tabs: PropsTab3ContentSection[] = [
  {
    title: (
      <>
        <Icon.Card className="parent" />
        <span>
          <Trans i18nKey="direction.Hướng dẫn mua hàng"></Trans>
        </span>
      </>
    ),
    content: <Content1 />,
  },
  {
    title: (
      <>
        <Icon.Pay className="parent" />
        <span>
          <Trans i18nKey="direction.Thanh toán các kỳ"></Trans>
        </span>
      </>
    ),
    content: <Content2 />,
  },
];

function DirectionMobile() {
  return (
    <Tab3
      title="direction.Hướng dẫn thanh toán"
      className="directtion-mobile"
      tabs={tabs}
    />
  );
}

export default DirectionMobile;
