import cx from 'classnames';
import React, { ReactElement, useState } from 'react';
import { useTranslation } from 'react-i18next';

export type PropsTab3ContentSection = {
  title: ReactElement;
  content: ReactElement;
};

type PropsTab3Section = {
  title: string;
  tabs: PropsTab3ContentSection[];
} & React.HTMLAttributes<HTMLDivElement>;

function Tab3({ title, className, tabs = [] }: PropsTab3Section) {
  const { t } = useTranslation();
  const [activeTab, setActiveTab] = useState(0);

  const handleActiveTab = (id: number) => {
    setActiveTab(id);
  };

  return (
    <div className={cx(['tab3', className])}>
      {title ? (
        <h3 className="container tab3__title color-grey-1000">{t(title)}</h3>
      ) : null}
      <ul className="tab3__controller">
        {tabs.map((ta, i) => (
          <li
            key={i}
            className={cx(['tab3__item', activeTab === i ? 'active' : ''])}
            onClick={() => handleActiveTab(i)}
          >
            {ta.title}
          </li>
        ))}
      </ul>
      <div className="tab3__content">
        {tabs.map((ta, i) => (
          <React.Fragment key={i}>
            {activeTab === i ? ta.content : null}
          </React.Fragment>
        ))}
      </div>
    </div>
  );
}

export default Tab3;
