import { Icon } from 'assets/svg';
import cx from 'classnames';
import { useTranslation } from 'react-i18next';

type PropsTwoShellSection1 = {
  title: string;
  imgUrl: string;
  list: string[];
  subTitle: string;
  theme?: 'light' | 'dark';
} & React.HTMLAttributes<HTMLDivElement>;

function TwoShellSection1({
  title,
  imgUrl,
  subTitle,
  className,
  list = [],
  theme = 'light',
}: PropsTwoShellSection1) {
  const { t } = useTranslation();

  return (
    <div className={cx(['two-shell', className])}>
      <div className="two-shell__item">
        <h4 className="two-shell__title">{t(title)}</h4>
        <p className={cx(['two-shell__subtitle', theme])}>{t(subTitle)}</p>
        <ul
          style={{
            listStyleImage: `url(${Icon.Check})`,
          }}
          className="two-shell__list"
        >
          {list.map((l, i) => (
            <li className={cx([theme])} key={i}>
              <Icon.Check className="parent" />
              <span>{t(l)}</span>
            </li>
          ))}
        </ul>
      </div>
      <div className="two-shell__item">
        <div className="two-shell__crop">
          <img className="cover-img" src={imgUrl} alt="imgage" />
        </div>
      </div>
    </div>
  );
}

export default TwoShellSection1;
