import { useWindowSize } from 'hooks';
import { Trans } from 'react-i18next';
import { isLargerMobile, isLargerTablet } from 'utils/helper';

type PropsHero = {
  title: string;
  heroUrl: string;
  heroTabletUrl: string;
  heroMobileUrl: string;
};

function Hero({ title, heroUrl, heroMobileUrl, heroTabletUrl }: PropsHero) {
  const [width] = useWindowSize();

  const renderBg = () => {
    if (isLargerTablet(width))
      return <img className="cover-img" src={heroUrl} alt="hero" />;
    if (isLargerMobile(width))
      return <img className="cover-img" src={heroTabletUrl} alt="hero" />;
    return <img className="cover-img" src={heroMobileUrl} alt="hero" />;
  };

  return (
    <div className="hero">
      {renderBg()}
      <div className="hero__filter1"></div>
      <div className="hero__filter2"></div>
      <div className="hero__filter3"></div>
      <div className="hero__content container">
        <h1>
          <Trans i18nKey={title}>
            Giải pháp <br />
            Mua Trước - Trả Sau <br /> Phổ Biến Nhất Việt Nam
          </Trans>
        </h1>
      </div>
    </div>
  );
}

export default Hero;
