import cx from 'classnames';
import FundiinButton from 'components/Common/FundiinButton';
import { ReactElement, ReactNode, useState } from 'react';
import { useTranslation } from 'react-i18next';

export type PropsTab1ContentSection = {
  title: ReactElement | ReactNode;
  content: ReactElement;
};

type PropsTab1Section = {
  title?: string;
  mini?: boolean;
  medium?: boolean;
  tabs:
    | PropsTab1ContentSection[]
    | ((active: boolean) => PropsTab1ContentSection[]);
  onTabChange?: (id: number) => void;
} & React.HTMLAttributes<HTMLDivElement>;

function Tab1({
  tabs,
  className,
  title = '',
  mini = false,
  medium = false,
  onTabChange = () => {},
}: PropsTab1Section) {
  const { t } = useTranslation();
  const [activeTab, setActiveTab] = useState(0);

  const handleActiveTab = (id: number) => {
    onTabChange(id);
    setActiveTab(id);
  };

  return (
    <div className={cx(['tab1', className])}>
      {title ? (
        <h3 className="tab1__title color-grey-1000">{t(title)}</h3>
      ) : null}
      <div className={cx([mini ? 'mini' : '', 'tab1__contronler'])}>
        {(typeof tabs === 'function' ? tabs(activeTab === 0) : tabs).map(
          (ta, i) => (
            <FundiinButton
              key={i}
              className={cx([
                mini ? 'mini' : '',
                medium ? 'medium' : '',
                activeTab === i ? 'active' : '',
              ])}
              onClick={() => handleActiveTab(i)}
            >
              {ta.title}
            </FundiinButton>
          )
        )}
      </div>
      <div className="tab1__content">
        {(typeof tabs === 'function' ? tabs(activeTab === 0) : tabs).map(
          (ta, i) =>
            activeTab === i ? (
              <div key={i} className="tab1__item">
                {ta.content}
              </div>
            ) : null
        )}
      </div>
    </div>
  );
}

export default Tab1;
