const TwoShellSection2Wrap = ({
  tabs = [],
}: {
  tabs: PropsTab1ContentSection[];
}) => {
  const dispatch = useDispatch();

  const handleChange = (id: number) => {
    let imgUrl = '';
    switch (id) {
      case 0:
        imgUrl = BuyContentStep1[0].img;
        break;
      case 1:
        imgUrl = BuyContentStep2[0].img;
        break;
      default:
        imgUrl = BuyContentStep3[0].img;
        break;
    }
    dispatch(change(imgUrl));
  };

  return (
    <TwoShellSection2
      rightContent={
        <Tab1
          mini
          tabs={tabs}
          className="direction--right"
          onTabChange={handleChange}
        />
      }
    />
  );
};

const tabs: (isActiveFirst: boolean) => PropsTab1ContentSection[] = active => [
  {
    title: (
      <>
        {active ? (
          <Icon.Card className="parent" />
        ) : (
          <Icon.CardOutline className="parent" />
        )}
        <span>
          <Trans i18nKey="direction.Hướng dẫn mua hàng"></Trans>
        </span>
      </>
    ),
    content: <TwoShellSection2Wrap tabs={BuyContent1} />,
  },
  {
    title: (
      <>
        <Icon.Pay className="parent" />
        <span>
          <Trans i18nKey="direction.Thanh toán các kỳ"></Trans>
        </span>
      </>
    ),
    content: (
      <TwoShellSection2
        rightContent={
          <Tab2 className="direction--right__pay" tabs={BuyContent2} />
        }
      />
    ),
  },
];

function Direction() {
  const dispatch = useDispatch();
  const [width] = useWindowSize();

  const handleChange = (id: number) => {
    switch (id) {
      case 0:
        dispatch(change(BuyContentStep1[0].img));
        break;

      default:
        dispatch(change(BuyContent2[0].img));
        break;
    }
  };

  return (
    <div className="container">
      <Tab1
        tabs={tabs}
        className="direction"
        medium={isSmallerPc(width)}
        title="direction.Hướng dẫn thanh toán"
        onTabChange={handleChange}
      />
    </div>
  );
}

export default Direction;
