import React, { useEffect, useMemo, useRef } from 'react';
import Carousel from 'react-multi-carousel';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from 'store';
import { change } from 'store/directionSlice';
import { mediaScreenMobile, mediaScreenTablet } from 'utils/constants';
import {
  BuyContent1,
  BuyContentStep1,
  BuyContentStep2,
  BuyContentStep3,
} from './_Direction';
import Tab2, { PropsTab2ContentSection } from './_Tabs2';

type PropsModalBody = {
  id: number;
  onTabChange?: (id: number) => void;
};

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: mediaScreenTablet },
    items: 1,
  },
  tablet: {
    breakpoint: { max: mediaScreenTablet, min: mediaScreenMobile },
    items: 1,
  },
  mobile: {
    breakpoint: { max: mediaScreenMobile, min: 325 },
    items: 1,
  },
};

const ModalBody1 = ({ id, onTabChange = () => {} }: PropsModalBody) => {
  const dispatch = useDispatch();
  const carousel = useRef<any>();
  const { activeImgUrl } = useSelector((state: RootState) => state.direction);

  const tabs = useMemo(() => {
    let data: PropsTab2ContentSection[] = [];
    switch (id) {
      case 0:
        data = BuyContentStep1;
        break;
      case 1:
        data = BuyContentStep2;
        break;
      default:
        data = BuyContentStep3;
        break;
    }

    return data;
  }, [id]);

  useEffect(() => {
    if (carousel.current) carousel.current?.goToSlide(id);
  }, [id]);

  return (
    <div className="directtion-mobile__body">
      <Carousel
        ref={carousel}
        responsive={responsive}
        renderArrowsWhenDisabled
        beforeChange={nextSlide => {
          onTabChange(nextSlide);
          switch (nextSlide) {
            case 0:
              dispatch(change(BuyContentStep1[0].img));
              break;
            case 1:
              dispatch(change(BuyContentStep2[0].img));
              break;
            default:
              dispatch(change(BuyContentStep3[0].img));
              break;
          }
        }}
      >
        {BuyContent1.map((_, i) => (
          <React.Fragment key={i}>
            <div className="directtion-mobile__img">
              <img className="cover-img" src={activeImgUrl} alt="image" />
            </div>
            <Tab2 tabs={tabs} className="directtion-mobile__tab2" />
          </React.Fragment>
        ))}
      </Carousel>
    </div>
  );
};

export default ModalBody1;
