import cx from 'classnames';
import { ReactElement } from 'react';
import { useSelector } from 'react-redux';
import { RootState } from 'store';

type PropsTwoShellSection2 = {
  rightContent: ReactElement;
} & React.HTMLAttributes<HTMLDivElement>;

function TwoShellSection2({ className, rightContent }: PropsTwoShellSection2) {
  const { activeImgUrl } = useSelector((state: RootState) => state.direction);

  return (
    <div className={cx(['two-shell2', className])}>
      <div className="two-shell2__item">
        <div className="two-shell2__crop">
          <img className="cover-img" src={activeImgUrl} alt="imgage" />
        </div>
      </div>
      <div className="two-shell2__item">{rightContent}</div>
    </div>
  );
}

export default TwoShellSection2;
