import { Icon } from 'assets/svg';
import cx from 'classnames';
import PrimaryButton from 'components/Common/PrimaryButton';
import { ReactElement } from 'react';
import { useTranslation } from 'react-i18next';

export type SubPropsFourShellSection = {
  subTitle: string;
  subContent: string;
  icon: ReactElement;
};

type PropsFourShellSection = {
  title: string;
  textBtn: string;
  shells: SubPropsFourShellSection[];
  onClickBtn: () => void;
} & React.HTMLAttributes<HTMLDivElement>;

function FourShellSection({
  title,
  textBtn,
  className,
  shells = [],
  onClickBtn = () => {},
}: PropsFourShellSection) {
  const { t } = useTranslation();

  return (
    <div className={cx(['four-shell', className])}>
      <div className="container">
        <h3 className="four-shell__title text-center color-grey-1000">
          {t(title)}
        </h3>
        <div className="four-shell__content">
          {shells.map((s, i) => (
            <div key={i} className="four-shell__item">
              <div className="four-shell__icon">{s.icon}</div>
              <h4 className="four-shell__subtitle color-primary-text text-center">
                {t(s.subTitle)}
              </h4>
              <p className="four-shell__subcontent color-secondary-text text-center">
                {t(s.subContent)}
              </p>
            </div>
          ))}
        </div>
        <div className="site-btn">
          <PrimaryButton className="register-btn" onClick={onClickBtn}>
            {t(textBtn)} <Icon.ArrowRight />
          </PrimaryButton>
        </div>
      </div>
    </div>
  );
}

export default FourShellSection;
